import Vue from 'vue'

/**
 * @desc 日期过滤器
 * @author hardy
 * @argument value
 * @argument type
 */
Vue.filter("fmtDate", function (value, type) {
  if (value == undefined || value == null) {
    return void 0
  }
  let dStr = isNaN(value) ? 0 : value;
  let date = new Date(dStr);
  let y = date.getFullYear();
  let m = date.getMonth() + 1;
  m = m < 10 ? ('0' + m) : m;
  let d = date.getDate();
  d = d < 10 ? ('0' + d) : d;
  let h = date.getHours();
  h = h < 10 ? ('0' + h) : h;
  let minute = date.getMinutes();
  let second = date.getSeconds();
  minute = minute < 10 ? ('0' + minute) : minute;
  second = second < 10 ? ('0' + second) : second;
  return y + '-' + m + '-' + d;
})
export function zeroizeOfTen (n) {
  return n < 10 ? ('0' + n) : n
}
export function formateDate (time) {
  let result = ''
  if (time) {
    let date = new Date(time)

    let y = date.getFullYear()
    let m = date.getMonth() + 1
    let d = date.getDate()

    m = zeroizeOfTen(m)
    d = zeroizeOfTen(d)

    result = y + '-' + m + '-' + d
  }
  return result
}


